html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

input {
  appearance: none;
}

input::-ms-clear {
  display: none;
}

a {
  text-decoration: none;
}

a:not([class]) {
  color: #2e99db;
  text-decoration: underline;
}

a:not([class]):hover {
  text-decoration: none;
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: local("Roboto Light"), local("Roboto-Light"), url("../fonts/roboto-light.woff2") format("woff2"), url("../fonts/roboto-light.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 300;
  src: local("Roboto Light Italic"), local("Roboto-LightItalic"), url("../fonts/roboto-lightitalic.woff2") format("woff2"), url("../fonts/roboto-lightitalic.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto Regular"), local("Roboto-Regular"), url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 400;
  src: local("Roboto Italic"), local("Roboto-Italic"), url("../fonts/roboto-italic.woff2") format("woff2"), url("../fonts/roboto-italic.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: local("Roboto Medium"), local("Roboto-Medium"), url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 500;
  src: local("Roboto MediumItalic"), local("Roboto-MediumItalic"), url("../fonts/roboto-mediumitalic.woff2") format("woff2"), url("../fonts/roboto-mediumitalic.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 700;
  src: local("Roboto Bold Italic"), local("Roboto-BoldItalic"), url("../fonts/roboto-bolditalic.woff2") format("woff2"), url("../fonts/roboto-bolditalic.woff") format("woff");
}

@font-face {
  font-family: "dancing_scriptregular";
  font-style: normal;
  font-weight: 400;
  src: local("dancing_scriptregular"), local("dancingscript-regular"), url("../fonts/dancingscript-regular.woff2") format("woff2"), url("../fonts/dancingscript-regular.woff") format("woff");
}

.icon-angle-down {
  width: 1.71em;
  height: 1em;
  fill: initial;
}

.icon-arrow-left {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-check {
  width: 1.3em;
  height: 1em;
  fill: initial;
}

.icon-question-option {
  width: 1em;
  height: 1em;
  fill: initial;
}

* {
  font-family: 'Roboto', Arial, sans-serif;
}

body {
  position: relative;
  min-width: 320px;
  color: #333;
  background: #fff;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

html,
body,
#root {
  height: 100%;
}

.main {
  flex-grow: 1;
  overflow: auto;
}

#root {
  display: flex;
  flex-direction: column;
}

[hidden] {
  display: none !important;
}

.pre-line {
  white-space: pre-line;
}

[data-accordion-item] [data-accordion-control] {
  cursor: pointer;
  user-select: none;
}

[data-accordion-item] [data-accordion-content] {
  display: none;
}

.input {
  padding: 0 12px;
  border: 1px solid #cccccc;
  border-radius: 0;
  width: 100%;
  height: 40px;
  background: #fff;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.input::-ms-clear {
  display: none;
}

.input:not([readonly]):focus {
  border-color: #2e99db;
}

.is-metro .input:not([readonly]):focus {
  border-color: #9784ff;
}

.input:disabled, .input[readonly] {
  border-color: #b3b3b3;
  background: #f2f2f2;
  cursor: not-allowed;
}

.input.is-error {
  border-color: #ec4a41;
}

.select {
  padding: 0 36px 0 12px;
  border: 1px solid #cccccc;
  border-radius: 0;
  width: 100%;
  height: 40px;
  background: url(../img/angle-down.svg) no-repeat #fff;
  background-position: right 12px center;
  background-size: auto 6px;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.select::-ms-expand {
  display: none;
}

.select:not([readonly]):focus {
  border-color: #2e99db;
}

.is-metro .select:not([readonly]):focus {
  border-color: #9784ff;
}

.select:disabled, .select[readonly] {
  border-color: #b3b3b3;
  background: #f2f2f2;
  cursor: not-allowed;
}

.select.is-error {
  border-color: #ec4a41;
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #fff;
  background: #2e99db;
  font-family: inherit;
  font-size: 18px;
  font-weight: 300;
  line-height: 50px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.is-metro .btn {
  background: #9784ff;
}

.btn:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.is-metro .btn:active {
  background: #6c51ff;
}

.btn:disabled, .btn.is-disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.btn_gray {
  background: #bec1cb;
}

.is-metro .btn_gray {
  background: #bec1cb;
}

.btn_gray:active {
  background: #a2a6b4;
}

.is-boost .btn_gray:active {
  background: #a2a6b4;
}

.is-metro .btn_gray:active {
  background: #a2a6b4;
}

.btn_red {
  background: #ec4a41;
}

.is-metro .btn_red {
  background: #ec4a41;
}

.btn_red:active {
  background: #e32117;
}

.is-metro .btn_red:active {
  background: #e32117;
}

.btn_wide {
  width: 100%;
}

.btn_plus {
  width: 40px;
  min-width: auto;
  height: 40px;
}

.btn_plus:before, .btn_plus:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 1px;
  background: currentColor;
  transform: translate(-50%, -50%);
}

.btn_plus:after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.label {
  color: #666666;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
}

.checkbox {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 30px;
  min-height: 30px;
  user-select: none;
}

.checkbox_middle .checkbox__text {
  padding-top: 5px;
  padding-bottom: 5px;
}

.checkbox input {
  display: none;
}

.checkbox input:checked ~ .checkbox__icon .icon {
  opacity: 1;
}

.checkbox input:disabled ~ .checkbox__icon, .checkbox input:disabled ~ .checkbox__text {
  opacity: 0.5;
  cursor: not-allowed;
}

.checkbox__icon {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #ccc;
  border-radius: 3px;
  width: 30px;
  height: 30px;
  background: #fff;
  cursor: pointer;
  transition: border-color 0.25s;
}

.checkbox__icon .icon {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  fill: #333;
  font-size: 14px;
  transform: translate3d(-50%, -50%, 0);
  transition: opacity 0.25s, background 0.25s;
}

.checkbox__text {
  display: inline-block;
  vertical-align: top;
  padding: 5px 0 5px 40px;
  cursor: pointer;
  transition: color 0.25s;
}

.header {
  position: relative;
  flex-shrink: 0;
  color: #fff;
  background: #2e99db;
  text-align: center;
}

.is-metro .header {
  background: #141547;
}

.header__help {
  right: 0;
  text-align: center;
  font-size: 0;
}

.header__help:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.header__help-icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 30px;
}

.header__help, .header__back {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 60px;
  color: #fff;
  background: none;
  cursor: pointer;
  transition: background 0.25s, box-shadow 0.25s;
}

.no-touch .header__help:hover, .no-touch .header__back:hover {
  background: #1f7db7;
}

.no-touch.is-metro .header__help:hover, .no-touch.is-metro .header__back:hover {
  background: #141547;
}

.header__help:active, .header__back:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.is-metro .header__help:active, .is-metro .header__back:active {
  background: #141547;
}

.header__back {
  left: 0;
  text-align: center;
  font-size: 0;
}

.header__back:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.header__back:active {
  box-shadow: inset -2px 2px 6px rgba(0, 0, 0, 0.2);
}

.header__back-icon {
  display: inline-block;
  vertical-align: middle;
  fill: #fff;
  font-size: 24px;
}

.header__inner {
  padding: 0 60px;
  height: 60px;
  text-align: center;
  font-size: 0;
}

.header__inner:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.header__title {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
}

.header__title span {
  text-transform: none;
}

.settings {
  padding: 40px 20px;
  margin: 0 auto;
  max-width: 800px;
}

.settings__section {
  border-radius: 3px;
  box-shadow: 0 2px 5px 1px rgba(51, 51, 51, 0.1);
}

.settings__section:not(:last-child) {
  margin-bottom: 20px;
}

.settings__header {
  padding: 30px;
  transition: color 0.25s;
}

.settings__header.is-active .settings__arrow {
  transform: rotate(180deg);
}

.no-touch .settings__header:hover {
  color: #2e99db;
}

.no-touch.is-metro .settings__header:hover {
  color: #9784ff;
}

.settings__title {
  position: relative;
  padding-right: 1.5em;
  font-size: 24px;
  font-weight: 700;
  line-height: 1;
}

.settings__arrow {
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -0.25em;
  fill: currentColor;
  font-size: 0.5em;
  transition: transform 0.25s;
}

.settings__inner {
  padding: 30px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.settings__row:not(:last-child) {
  margin-bottom: 30px;
}

.settings__table {
  margin: -8px;
}

.settings__tr {
  position: relative;
  display: flex;
  padding-right: 40px;
}

.settings__td {
  padding: 8px;
  flex-grow: 1;
  flex-shrink: 1;
}

.settings__td_lg {
  flex-basis: 60%;
}

.settings__td_sm {
  flex-basis: 40%;
}

.settings__tfoot {
  padding-top: 10px;
}

.settings__remove {
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -20px;
  width: 40px;
  height: 40px;
  background: none;
}

.settings__remove span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotate(45deg);
}

.settings__remove span:before, .settings__remove span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 1px;
  background: currentColor;
  transform: translate(-50%, -50%);
}

.settings__remove span:after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.settings__footer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: -10px;
}

.settings__footer-btn {
  padding: 10px;
  width: 100%;
  max-width: 140px;
}

.form-group__label {
  margin-bottom: 8px;
}

.form-group__description, .form-group__control_inline {
  display: inline-block;
  vertical-align: top;
}

.form-group__control_inline {
  width: 140px;
}

.form-group__description {
  padding: 8px 16px 8px 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

.form-group__input_center {
  text-align: center;
}
