.header {
	position: relative;
	flex-shrink: 0;
	color: $white;
	background: $blue;
	text-align: center;
	@include metro {
		background: $metroPrimary; }
	&__help {
		right: 0;
		@include vertical;
		&-icon {
			display: inline-block;
			vertical-align: middle;
			fill: currentColor;
			font-size: 30px; } }
	&__help,
	&__back {
		position: absolute;
		top: 0;
		bottom: 0;
		width: 60px;
		color: $white;
		background: none;
		cursor: pointer;
		transition: background 0.25s, box-shadow 0.25s;
		@include hover {
			background: darken($blue, 10%); }
		@include hover('metro') {
			background: $metroPrimary; }
		&:active {
			background: darken($blue, 10%);
			box-shadow: inset 2px 2px 6px rgba(0,0,0,.2);
			@include metro {
				background: $metroPrimary; } } }
	&__back {
		left: 0;
		@include vertical;
		&:active {
			box-shadow: inset -2px 2px 6px rgba(0,0,0,.2); }
		&-icon {
			display: inline-block;
			vertical-align: middle;
			fill: $white;
			font-size: 24px; } }
	&__inner {
		padding: 0 60px;
		height: 60px;
		@include vertical; }
	&__title {
		display: inline-block;
		vertical-align: middle;
		@include f(18px, 500, 1.2);
		text-transform: uppercase;
		span {
			text-transform: none; } } }
