.form-group {
	&__label {
		margin-bottom: 8px; }
	&__description,
	&__control_inline {
		display: inline-block;
		vertical-align: top; }
	&__control_inline {
		width: 140px; }
	&__description {
		padding: 8px 16px 8px 0;
		@include f(18px, 400, 24px); }
	&__input_center {
		text-align: center; } }
