@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin gpu {
  transform: translate3d(0,0,0); }

//width
@mixin md {
  @include r(620) {
    @content; } }

@mixin sm {
  @include r(580) {
    @content; } }

@mixin xs {
  @include r(480) {
    @content; } }
//height
@mixin hsm {
  @include rhmin(640) {
    @content; } }
@mixin hxs {
  @include rhmin(481) {
    @content; } }



@mixin f($font-size: null, $font-weight: null, $line-height: null) {
  @if $font-size != null {
    font-size: $font-size; }
  @if $font-weight != null {
    font-weight: $font-weight; }
  @if $line-height != null {
    line-height: $line-height; } }

@mixin boost {
  .is-boost & {
    @content; } }

@mixin metro {
  .is-metro & {
    @content; } }
